	queryMessageList() {
		let par = {
			userId: JSON.parse(this.commfunc.getSessionData('commonHeader')).userId,
			sendChannel: "6",
			eventId: 'wf0000002',
			status: '0'
		}
		let ob = this.httpService.queryMessageList(par);
		ob.subscribe(data => {
			this.daat = this.commfunc.transDateN(this.date);
			let a = data.messageList.splice(0, 5);
			a.forEach(el => {
				if (el.createdStamp) {
					el.createdStamp = el.createdStamp.slice(0, 10)
				}
			});
			this.newMsgs = _.clone(a);
		})
	}

