	//  处理客户联系数据数据
	custcode() {
		let pram = {
			taskPoolFlag: '3',
			pageRows: 10000,
			curPage: 1,
			itemType: 'KHLX',
			itemNode: "0308_01",
			userId: JSON.parse(this.commfunc.getSessionData('commonHeader')).userId,
		}
		this.httpService.bpmQueryUnDoTaskList(pram).subscribe(data => {
			if (data.returnCode.code == 'success') {
				let list = data.pageData.result;
				// console.log(list)
				// 处理好的数据
				let array = [
					{ time: '', list: [], sysnum: 0 },
					{ time: '', list: [], sysnum: 0 },
					{ time: '', list: [], sysnum: 0 },
					{ time: '', list: [], sysnum: 0 },
					{ time: '', list: [], sysnum: 0 },
					{ time: '', list: [], sysnum: 0 },
					{ time: '', list: [], sysnum: 0 },
				];
				// 7天内的数据
				let qiArray = [];
				// 获取当前时间
				let time = this.commfunc._getNowDate();
				let q = Number(time) + 7;
				// 处理时间格式
				// let list1 = list.sort((A, B) => B.varMap.dataMap.fromDate - A.varMap.dataMap.fromDate);
				// console.log(list1)
				let chaoshi = [];
				let zhengchang = [];
				let allcszc = [];
				for (let k = 0; k < list.length; k++) {
					const e = list[k];
					e['date'] = this.commfunc.transDateNew(e.varMap.dataMap.fromDate);
					e.createDate = e.varMap.dataMap.fromDate.slice(0, 10);
					// 获取数据
					if (e.date <= q) {
						if (e.date == time || e.date < time) {
							let alllength = chaoshi.length + zhengchang.length;
							if (alllength < 4) {
								array[0].time = '今天';
								if (e.date < time) {
									e['chaoshi'] = 'Y';
									let a = e.date.slice(0, 4); let b = e.date.slice(4, 6); let c = e.date.slice(6, 8); e.date = b + '-' + c;
									chaoshi.push(e);
								} else {
									e.date = e.varMap.dataMap.fromDate.slice(11, 16)
									zhengchang.push(e);
								}
							}
							array[0].sysnum = array[0].sysnum + 1
						} else if (e.date == (Number(time) + 1)) {
							let day = this.commfunc.getDay(e.createDate)
							array[1].time = e.createDate + " " + day;
							if (array[1].list.length < 4) {
								array[1].list.push(e);
							}
							e.date = e.varMap.dataMap.fromDate.slice(11, 16)
							array[1].sysnum = array[1].sysnum + 1
						} else if (e.date == (Number(time) + 2)) {
							let day = this.commfunc.getDay(e.createDate)
							array[2].time = e.createDate + " " + day;
							if (array[2].list.length < 4) {
								array[2].list.push(e);
							}
							e.date = e.varMap.dataMap.fromDate.slice(11, 16)
							array[2].sysnum = array[2].sysnum + 1
						} else if (e.date == (Number(time) + 3)) {
							let day = this.commfunc.getDay(e.createDate)
							array[3].time = e.createDate + " " + day;
							if (array[3].list.length < 4) {
								array[3].list.push(e);
							}
							e.date = e.varMap.dataMap.fromDate.slice(11, 16)
							array[3].sysnum = array[3].sysnum + 1
						} else if (e.date == (Number(time) + 4)) {
							let day = this.commfunc.getDay(e.createDate)
							array[4].time = e.createDate + " " + day;
							if (array[4].list.length < 4) {
								array[4].list.push(e);
							}
							e.date = e.varMap.dataMap.fromDate.slice(11, 16)
							array[4].sysnum = array[4].sysnum + 1
						} else if (e.date == (Number(time) + 5)) {
							let day = this.commfunc.getDay(e.createDate)
							array[5].time = e.createDate + " " + day;
							if (array[5].list.length < 4) {
								array[5].list.push(e);
							}
							e.date = e.varMap.dataMap.fromDate.slice(11, 16)
							array[5].sysnum = array[5].sysnum + 1
						} else if (e.date == (Number(time) + 6)) {
							let day = this.commfunc.getDay(e.createDate)
							array[6].time = e.createDate + " " + day;
							if (array[6].list.length < 4) {
								array[6].list.push(e);
							}
							e.date = e.varMap.dataMap.fromDate.slice(11, 16)
							array[6].sysnum = array[6].sysnum + 1
						}
					}
				}
				allcszc = chaoshi.concat(zhengchang);
				array[0].list = array[0].list.concat(allcszc)
				// 删除空数据
				for (let i = array.length - 1; i >= 0; i--) {
					if (!array[i].time) {
						array.splice(i, 1)
					}
				}
				console.log("处理好的数据")
				console.log(array)
				this.tmapArray = array
				let sum = 0;
				this.tmapArray.forEach(element => {
					sum += element.sysnum
				});
				// 计算7天内的数据
				if (sum >= 100) {
					this.workblank3 = '99+'
				} else {
					this.workblank3 = sum
				}
				// 左右移动按钮显示
				if (this.tmapArray.length > 3) {
					this.showFlagrRight = true;
				} else {
					this.showFlagrRight = false;
				}
				this.Tmap4 = []; this.Tmap5 = []; this.Tmap6 = [];
				if (this.tmapArray.length > 0) {
					this.Tmap4.push(this.tmapArray[0]);
				}
				if (this.tmapArray.length > 1) {
					this.Tmap5.push(this.tmapArray[1]);
				}
				if (this.tmapArray.length > 2) {
					this.Tmap6.push(this.tmapArray[2]);
				}
				if (0 < this.tmapArray.length && this.tmapArray.length < 4) {
					this.allpage = 1;
				} else if (3 < this.tmapArray.length && this.tmapArray.length < 7) {
					this.allpage = 2;
				} else {
					this.allpage = 3;
				}
			}
		})
		this.page = 1;
	}
	// 客户联系左划按钮
	left() {
		this.page--;
		this.Tmap4 = []; this.Tmap5 = []; this.Tmap6 = [];
		if (this.page == 2) {
			this.showFlagrRight = true;
			this.showFlagrLeft = true;
			this.Tmap4.push(this.tmapArray[3]);
			this.Tmap5.push(this.tmapArray[4]);
			this.Tmap6.push(this.tmapArray[5]);
		} else if (this.page == 1) {
			this.showFlagrRight = true;
			this.showFlagrLeft = false;
			this.Tmap4.push(this.tmapArray[0]);
			this.Tmap5.push(this.tmapArray[1]);
			this.Tmap6.push(this.tmapArray[2]);
		}
	}

	// 客户联系右划按钮
	right() {
		// 第几页
		this.page++;
		this.Tmap4 = []; this.Tmap5 = []; this.Tmap6 = [];
		if (this.page == 2) {
			if (this.allpage == 3) {
				this.showFlagrRight = true;
				this.showFlagrLeft = true;
				this.Tmap4.push(this.tmapArray[3]);
				this.Tmap5.push(this.tmapArray[4]);
				this.Tmap6.push(this.tmapArray[5]);
			} else {
				this.showFlagrRight = false;
				this.showFlagrLeft = true;
				if (this.tmapArray.length == 6) {
					this.Tmap4.push(this.tmapArray[3]);
					this.Tmap5.push(this.tmapArray[4]);
					this.Tmap6.push(this.tmapArray[5]);
				} else if (this.tmapArray.length == 5) {
					this.Tmap4.push(this.tmapArray[3]);
					this.Tmap5.push(this.tmapArray[4]);
				} else {
					this.Tmap4.push(this.tmapArray[3]);
				}
			}
		} else if (this.page == 3) {
			this.showFlagrRight = false;
			this.showFlagrLeft = true;
			this.Tmap4.push(this.tmapArray[6]);
		}
	}

