	//删除权限
	deleteAuthority(item) {
		let param = {
			userId: this.user.userId,
			authorityList: item,
		}
		this.httpService.deleteAuthority(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.httpService.queryAuthority(param).subscribe(data => {
					if (data.returnCode.code == 'success') {
						this.functionList = data.authorityList;
					}
				})
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
			}
		}, err => {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
		})
	}

