	getIdData() {
		if (JSON.parse(this.commfunc.getSessionDataCH('menu')) != null) {
			if (JSON.stringify(JSON.parse(this.commfunc.getSessionDataCH("menu")).menu) != "{}") {
				let mp = new Map();
				let butList = JSON.parse(this.commfunc.getSessionDataCH("menu")).menu;
				let rays = [];
				for (let i of butList) {
					rays.push(...this.menuitems(i))
				}
				this.idData = rays;
			}
		}
	}

	MenuSelect(menuId) {
		for (let i of this.idData) {
			if (i.sourceId == menuId) {
				return true;
			}
		}

	}

	menuitems(item) {
		this.arr.push({ 'sourceId': item.sourceId })
		if (item.items) {
			for (let i of item.items)
				this.menuitems(i)
		}
		return this.arr;
	}

	//获取当前时间
	transDateN() {

		let time = new Date();
		let year = time.getFullYear();
		let month = time.getMonth() + 1 + '';
		let data = time.getDate() + '';
		if (month.length < 2) {
			month = '0' + month;
		}
		if (data.length < 2) {
			data = "0" + data
		}
		let newtime = year + "-" + month + "-" + data;
		return newtime;
	}

