import { Component, OnInit, Input, AfterViewInit, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { Message } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
@Component({
    selector: 'common-function',
    templateUrl: './common-function.component.html',
    styleUrls: ['./common-function.component.scss'],
    providers: [CustomeHttpService]
})
export class CommonFunctionComponent implements OnInit {
    @Input() inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    msgs: Message[] = [];
    userId: string;
    functionList: any;
    CheckBox: boolean;
    checkAddList: any = [];
    leng: number;
    constructor(
        public commfunc: Commfunc,
        public httpService: CustomeHttpService,
    ) {
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
    }

    ngOnInit() {
        this.leng = this.inValue.length * 1;
        this.commonFunction();
    }

    //常用功能--李聪
    commonFunction() {
        let param = { tellerId: this.userId }
        this.httpService.getWebAppSourceList(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.functionList = data.webAppSourceList;

                for (let i of data.webAppSourceList) {
                    for (let item of i.items) {
                        for (let j of this.inValue) {
                            if (item.sourceId == j.authorityId) {
                                item['CheckBox'] = true;
                            }
                        }
                    }
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        })
    }

    //修改常用功能
    createOrStorePublicAuthority(items) {
        let arrList = [];
        items.forEach(item => {
            for (let temp of item.items) {
                if (temp.CheckBox) {
                    let arr = {
                        authorityId: temp.sourceId,
                        authorityName: temp.description,
                        url: temp.linkUrl
                    }
                    arrList.push(arr)
                }
            }
        });
        let param = {
            userId: this.userId,
            authorityList: arrList
        }
        this.httpService.createOrStorePublicAuthority(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.outValue.emit(false);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        })
    }
    checkAdd(item, temp) {
        if (item == true) {
            // temp.CheckBox = false;
            // return;
            if (this.leng < 8) {
                this.leng = this.leng + 1;
                this.checkAddList.push(item)
            } else {
                temp.CheckBox = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: "最多选择八个" });
            }

        } else {
            this.leng = this.leng - 1;
            this.checkAddList.forEach((tep, i) => {
                if (item.taskCategoryId == tep.taskCategoryId) {
                    this.checkAddList.splice(i, 1)
                }
            });
        }
    }
}
